@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.Home, .Page70, .Page80, .Page90, .Page00, .Page10, .Page20{
    min-height: 100vh;
    position: relative;

}

.Home::before, .Page00::before, .Page10::before, .Page20::before, .Page90::before, .Page70::before, .Page80::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

}

.Home::before, .Page80::before, .Page00::before, .Page20::before   {
    background: url(/mustang_project/static/media/wallpaper.90773a23.jpg) no-repeat;
    background-size: cover;
}
.Page70::before,.Page90::before, .Page10::before {
    background: url(/mustang_project/static/media/wallpaper70.6be68c12.jpg) no-repeat;
    background-size: cover;
}


:root {
    --primary: #fff;

}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn-medium:hover, .btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}
.navbar {
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
    background: transparent;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
    position: relative;
    top: 0;
    z-index: 999;
    width: 100%;
  }
  
  .navbar_container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar_logo {
    cursor: pointer;
    text-decoration: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 140px;
  }
  .navbar_logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }

  .fas {
    color: #fff
  }
  
  .nav_menu {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    text-align: center;
    width: 60vw;
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 2rem;

  }
  
  .nav_items {
    height: 80px;
  }
  
  .nav_links {
    /* color: #fff; */
    color: #000;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }

  .nav_links.active {
    border-bottom: 4px solid #880000;
    margin-bottom: -4px;
  }
  
  .nav_links:hover {
    border-bottom: 4px solid #880000;
    margin-bottom: -4px;
    /* transition: all 0.2s ease-out; */
  }
  
  .fabars {
    color: #fff;
  }
  
  .nav_links_mobile {
    display: none;
  }
  
  .menu_icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {


    .NavbarItems {
      position: relative;
    }
  
    .nav_menu {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100%;
      min-height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav_menu.active {
      background: #242222;
      /* background: red; */
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav_links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav_links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
    .nav_links.active {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar_logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu_icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa_times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav_links_mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav_links_mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }
.circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: #017143; */
    background: #e51e2a;
    -webkit-clip-path: circle(64.4% at 73% 64%);
            clip-path: circle(64.4% at 73% 64%);
}

.small_circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background: #017143; */
    background: #e25660;
    -webkit-clip-path: circle(64.4% at 100% 90%);
            clip-path: circle(64.4% at 100% 90%);
}
